.dashboard-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 16px;
  }
  
  .tabs-list {
    display: flex;
    gap: 8px;
    margin-bottom: 16px;
  }
  
  .chart-card {
    margin-bottom: 16px;
  }
  
  .chart-title {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 8px;
  }
  
  .dropdown-container {
    display: flex;
    justify-content: center;
    margin-bottom: 16px;
  }
  
  .dropdown-trigger {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 12px;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    background-color: white;
    cursor: pointer;
    font-size: 16px;
    width: 200px;
  }
  
  .dropdown-icon {
    width: 16px;
    height: 16px;
    margin-left: 8px;
  }
  
  .select-content {
    background-color: white;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    margin-top: 4px;
  }
  